import { UPDATE_TIME, SELECT_DATE, ADD_DATE, TO_TODAY } from "../actions/types";
const moment = require("moment");

const initialState = {
  currentYear: "",
  currentMonth: "",
  currentDate: "",
  currentDay: "",
  currentHour: "",
  currentMinute: "",
  targetYear: moment().format("YYYY"),
  targetMonth: moment().format("MM"),
  targetDay: parseDayToJa(moment().isoWeekday()),
  targetDate: moment().format("DD")
};

function parseDayToJa(Day) {
  switch (Day) {
    case 1:
      return "月";
    case 2:
      return "火";
    case 3:
      return "水";
    case 4:
      return "木";
    case 5:
      return "金";
    case 6:
      return "土";
    case 7:
      return "日";
  }
}

export default function(state = initialState, action) {
  const nextState = Object.assign({}, state);
  switch (action.type) {
    case UPDATE_TIME:
      const datetime = moment();
      nextState.currentDay = parseDayToJa(datetime.isoWeekday());
      nextState.currentYear = datetime.format("YYYY");
      nextState.currentMonth = datetime.format("MM");
      nextState.currentDate = datetime.format("DD");
      nextState.currentHour = datetime.format("HH");
      nextState.currentMinute = datetime.format("mm");
      return nextState;
    case SELECT_DATE:
      nextState.targetYear = action.payload.format("YYYY");
      nextState.targetMonth = action.payload.format("MM");
      nextState.targetDate = action.payload.format("DD");
      nextState.targetDay = parseDayToJa(action.payload.isoWeekday());
      return nextState;
    case ADD_DATE:
      const targetDatetime = moment().set({
        year: state.targetYear,
        month: state.targetMonth - 1,
        date: state.targetDate
      });
      targetDatetime.add(action.payload, "day");

      nextState.targetYear = targetDatetime.format("YYYY");
      nextState.targetMonth = targetDatetime.format("MM");
      nextState.targetDate = targetDatetime.format("DD");
      nextState.targetDay = parseDayToJa(targetDatetime.isoWeekday());
      return nextState;
    case TO_TODAY:
      nextState.targetYear = action.payload.format("YYYY");
      nextState.targetMonth = action.payload.format("MM");
      nextState.targetDate = action.payload.format("DD");
      nextState.targetDay = parseDayToJa(action.payload.isoWeekday());
      return nextState;
    default:
      return nextState;
  }
}
