import {
    SELECT_DATE,
    REQUEST_DELETE,
    SEND_PATCH,
    SEND_POST,
    FETCH_ALL_SCHEDULES
  } from "../actions/types";


const initialState = {
    schedules :[],
}

export default function(state=initialState,action){
    switch(action.type){
        case FETCH_ALL_SCHEDULES:
        return {schedules:action.payload}
        case REQUEST_DELETE,SEND_PATCH,SEND_POST:
        return state
        default:
        return state
}
}